import React, { useState } from 'react';
import ReactDOM from 'react-dom';
import './index.css';

function NameForm() {
  const sendRequestToAPI = (url, htmlSelector, responseKey) => {
    let nameQuery = document.querySelector('#nameForm').value;
    fetch(
      `${url}?name=${nameQuery}`
    ).then(response => response.json())
    .then(data => {
      document.querySelector(htmlSelector).textContent=data[responseKey]
    })
  }
  const handleSubmit = (evt) => {
    evt.preventDefault();
    sendRequestToAPI('https://api.genderize.io', '#gender', 'gender');
    sendRequestToAPI('https://api.agify.io', '#age', 'age');
  }

  // re-add onSubmit={handleSubmit} later
  return (
    <form onSubmit={handleSubmit}>
      <input class="input is-medium is-primary-light my-3"
        name="name"
        type="text"
        id="nameForm"
      />
    </form>
  )
}

function Page() {
  return (
    <div class="container">
      <div class="hero is-primary has-text-weight-bold is-size-2 has-text-centered">
        <div class="columns">
          <div class="column"></div>
          <div class="column">
          Enter your first name below
          <NameForm />
          </div>
          <div class="column"></div>
        </div>
      </div>
      <div class="box">
        <div class="columns is-mobile">
          <div class="column">Best Gender Guess:</div>
          <div class="column" id="gender">Not submitted</div>
          <div class ="column">Guess provided by <a href="https://genderize.io">genderize.io</a></div>
        </div>
        <div class="columns is-mobile">
          <div class="column">Best Age Guess:</div>
          <div class="column" id="age">Not submitted</div>
          <div class ="column">Guess provided by <a href="https://agify.io">agify.io</a></div>
        </div>
      </div>
    </div>
  );
}

ReactDOM.render(
  <Page />,
  document.getElementById('root')
);

